<template>
    <div class="preloader" v-if="loading">
      <div class="loader"></div>
    </div>
    <div v-if="loading === false">
      <div> 
        <h2>Список поточних фільмів</h2>
        <div class="films-main">
            <div class="film-main" v-for="film in films" :key="film.id">
                <div class="image-delete">
                  <img :src="film.image" class="imgInput" v-if="film.image" @click="filmClick(film.id)">
                  <span class="deleteSpan2" v-if="film.image  && films.length > 1" @click="deleteFilm(film.id)">x</span>
                </div>
                <p @click="filmClick(film.id)">{{ film.title }}</p>
            </div>
        </div>
    </div>
    <div class="mt">
        <h2>Список фільмів, які скоро вийдуть</h2>
        <div class="films-main">
            <div class="film-main" v-for="film in films2" :key="film.id">
                <div class="image-delete">
                  <img :src="film.image" class="imgInput" v-if="film.image" @click="filmClick(film.id)">
                  <span class="deleteSpan2" v-if="film.image && films2.length > 1" @click="deleteFilm2(film.id)">x</span>
                </div>
                <p @click="filmClick(film.id)">{{ film.title }}</p>
            </div>
        </div>
    </div>
    </div>
    
</template>


<script>    

import { ref, onMounted } from 'vue';
import { getDatabase, ref as dbRef, onValue, remove } from 'firebase/database';
import { getStorage, ref as storRef, deleteObject, listAll } from "firebase/storage";

export default {

    setup() {
    const db = getDatabase()
    const moviesRef = dbRef(db, 'films');

    const loading = ref(true)
    const films = ref([]);
    const films2 = ref([]);

    onMounted(() => {
      onValue(moviesRef, (snapshot) => {
        const moviesData = snapshot.val();
        const moviesArray = [];
        const moviesArray2 = [];

        for (const key in moviesData) {
          if(moviesData[key].radioData === 'Вже вийшов'){
            moviesArray.push({
              id: moviesData[key].id,
              title: moviesData[key].nameFilm,
              image: moviesData[key].imageFirst,
            });
          } else{
            moviesArray2.push({
              id: moviesData[key].id,
              title: moviesData[key].nameFilm,
              image: moviesData[key].imageFirst,
            });
          }
          
        }

        films.value = moviesArray;
        films2.value = moviesArray2;

        setTimeout(() => {
          loading.value = false;
        }, 2000);
      });
    });
    return { films, films2, loading };
  },


    data(){
        return{

        }
    },


    methods: {
      filmClick(id) {
        this.$router.push({ name: "film", params: { id } });
      },
      deleteFilm(id){
        const db = getDatabase();
        const pathDb = `films/${id}`
        const itemRef = dbRef(db, pathDb);

        if(this.films.length > 1){

        remove(itemRef)
          .then(() => {
            console.log("Item successfully deleted from Realtime Database");
          })
          .catch((error) => {
            console.log("Error deleting item from Realtime Database: ", error);
          });
        
          const storage = getStorage();
          const pathStor = `films/${id}/galleryImgs`

          // Видалення фотографій фільму
           const filmPhotosRef = storRef(storage, pathStor);

            listAll(filmPhotosRef).then((result) => {
            result.items.forEach((imageRef) => {
              // Видаляємо кожну фотографію
              deleteObject(imageRef).then(() => {
                console.log(`Фотографія ${imageRef.name} була успішно видалена!`);
              }).catch((error) => {
                console.log(`Сталася помилка при видаленні фотографії ${imageRef.name}:`, error);
              });
            });
          });
           
          // // Видалення головної фотографії фільму
          const filmMainPhotoRef = storRef(storage, `films/${id}/imageFirst`);
          deleteObject(filmMainPhotoRef);

          // Видалення папки фільму
          // const filmRef = storRef(storage, `films/${id}`);
          // deleteObject(filmRef);
        }

      },
      deleteFilm2(id){
        const db = getDatabase();
        const pathDb = `films/${id}`
        const itemRef = dbRef(db, pathDb);

        if(this.films2.length > 1){

        remove(itemRef)
          .then(() => {
            console.log("Item successfully deleted from Realtime Database");
          })
          .catch((error) => {
            console.log("Error deleting item from Realtime Database: ", error);
          });
        
          const storage = getStorage();
          const pathStor = `films/${id}/galleryImgs`

          // Видалення фотографій фільму
           const filmPhotosRef = storRef(storage, pathStor);

            listAll(filmPhotosRef).then((result) => {
            result.items.forEach((imageRef) => {
              // Видаляємо кожну фотографію
              deleteObject(imageRef).then(() => {
                console.log(`Фотографія ${imageRef.name} була успішно видалена!`);
              }).catch((error) => {
                console.log(`Сталася помилка при видаленні фотографії ${imageRef.name}:`, error);
              });
            });
          });
           
          // // Видалення головної фотографії фільму
          const filmMainPhotoRef = storRef(storage, `films/${id}/imageFirst`);
          deleteObject(filmMainPhotoRef);

          // Видалення папки фільму
          // const filmRef = storRef(storage, `films/${id}`);
          // deleteObject(filmRef);
        }

      },
    },

}
</script>


<style lang="scss">

.films-main{
    display: flex;
    gap: 40px;
    width: 92%;
    margin: 30px auto 0;
}
.film-main{
    display: flex;
    flex-direction: column;
    gap: 10px;
    cursor: pointer;

    img{
        width: 250px;
        height: 250px;
        display: block;
    }

    p{
        font-weight: 600;
        font-size: 20px;
        color: #515558;
    }
}
.mt{
  margin-top: 40px;
}
.deleteSpan2{
  position: absolute;
  top: -8px;
  right: -8px;
  border: 1px solid black;
  background: white;
  border-radius: 50%;
  color: black;
  width: 26px;
  z-index: 2;
}
.image-delete{
  position: relative;
}
    

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #333333;
  opacity: 0.7;
  z-index: 99999;
}

.loader {
  border: 16px solid #333333;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 1s linear infinite;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -60px;
  margin-left: -60px;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

</style>